import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import "./AniList.scss";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Skeleton from "@mui/material/Skeleton";
import Box from "@mui/material/Box";

import axios from "axios";

import { getShikiApi } from "../../redux/reducer";

const AniList = () => {
	const state = useSelector((state) => state.anilist);
	const dispatch = useDispatch();

	const [url, seturl] = useState(
		"https://shikimori.one/api/animes?limit=50&order=popularity"
	);

	useEffect(() => {
		const source = axios.CancelToken.source();
		axios
			.get(url, {
				cancelToken: source.token,
			})
			.then((response) => {
				dispatch(getShikiApi(response.data));
			})
			.catch((error) => {
				console.log(error);
			});
		return source.cancel;
	}, [url]);

	const sceleton = (
		<Box
			sx={{
				bgcolor: "#121212",
				p: 8,
				width: "100%",
				display: "flex",
				justifyContent: "center",
			}}
		>
			<Skeleton
				sx={{ bgcolor: "grey.900" }}
				variant="rectangular"
				width={210}
				height={118}
			/>
		</Box>
	);

	const anime =
		state.anilist.length < 1
			? sceleton
			: state.anilist.map((item) => {
					return (
						<Card sx={{ maxWidth: 200, minWidth: 200 }} key={item.id} className="ani-card">
							<CardMedia
								component="img"
								height="140"
								// image="/img/ava.jpg"
								image={'https://shikimori.one/' + item.image.preview.split('?')[0]}
								alt="green iguana"
							/>
							<CardContent>
								<Typography
									gutterBottom
									variant="h5"
									component="div"
								>
									{item.name}
								</Typography>
								<Typography
									variant="body2"
									color="text.secondary"
								>
									Score: {item.score}
								</Typography>
							</CardContent>
							<CardActions>
								<Button size="small">+</Button>
								<Button size="small">Learn More</Button>
							</CardActions>
						</Card>
					);
			  });

	return <section className="anilist">{anime}</section>;
};

export default AniList;
